@import url("https://fonts.googleapis.com/css?family=Ubuntu|Lato:400,700");
html {
  font-size: 75%; }

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }
  * :not(h1), * :not(h2), * :not(h3), * :not(h4), * :not(h5), * :not(h6) {
    font-family: "Ubuntu", sans-serif;
    color: #FDFFFE; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Lato", sans-serif;
  font-weight: bold;
  color: #FDFFFE; }

body {
  background: #F71735;
  /* Old browsers */
  /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, #FCAB10 0%, #F71735 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: -o-linear-gradient(45deg, #FCAB10 0%, #F71735 100%);
  background: linear-gradient(45deg, #FCAB10 0%, #F71735 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=$yellow, endColorstr=$red,GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  background-repeat: no-repeat;
  background-size: 100% 250vh;
  overflow-x: hidden; }
  @media all and (max-width: 600px) {
    body {
      background-size: 100% 350vh; } }

#about-section {
  min-height: 100vh;
  background: #fcab10;
  /* Old browsers */
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, #fcab10 0%, #f8312e 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(315deg, #fcab10 0%, #f8312e 100%);
  background: -o-linear-gradient(315deg, #fcab10 0%, #f8312e 100%);
  background: linear-gradient(135deg, #fcab10 0%, #f8312e 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fcab10', endColorstr='#f8312e',GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */ }

::-webkit-scrollbar {
  width: 1em;
  height: 1em; }

::-webkit-scrollbar-button {
  width: 0px;
  height: 0px; }

::-webkit-scrollbar-thumb {
  background: #A9BCD0;
  border-radius: 2em; }
  ::-webkit-scrollbar-thumb:hover {
    background: #FCAB10; }
  ::-webkit-scrollbar-thumb:active {
    background: #F71735; }

::-webkit-scrollbar-track, ::-webkit-scrollbar-track-piece {
  background: #011627; }
  ::-webkit-scrollbar-track:hover, ::-webkit-scrollbar-track-piece:hover {
    background: #FCAB10; }
  ::-webkit-scrollbar-track:active, ::-webkit-scrollbar-track-piece:active {
    background: #011627; }

::-webkit-scrollbar-corner {
  background: transparent; }

.section-container {
  display: grid;
  display: -ms-grid;
  grid-template-columns: minmax(2.5rem, 3vw) 1fr minmax(2.5rem, 3vw);
  -ms-grid-columns: minmax(2.5rem, 3vw) 1fr minmax(2.5rem, 3vw);
  grid-template-rows: 1fr;
  -ms-grid-rows: 1fr;
  -ms-flex-align: start;
      align-items: flex-start;
  padding: 5vw 3vw; }
  .section-container .content-container {
    padding: 5em 5vw; }

.sticky {
  margin-top: 8em; }

.section-title {
  opacity: 0.7;
  -ms-flex-item-align: start;
      align-self: start;
  padding-top: 5em; }

p {
  font-size: 1.75rem;
  line-height: 2.25rem; }

li {
  font-size: 1.5rem;
  line-height: 2rem; }

.statement {
  font-size: 3rem;
  line-height: 3.5rem;
  font-family: "Lato";
  font-weight: bold;
  text-shadow: 0.2rem 0.2rem 0.2rem rgba(0, 0, 0, 0.1); }

.small-text {
  font-size: 1.2rem;
  line-height: 1.25rem; }

h1 {
  font-size: 3rem; }

h2 {
  font-size: 2.5rem; }

h3 {
  font-size: 2.25rem; }

h4 {
  font-size: 2rem; }

@media all and (max-width: 1000px) {
  p {
    font-size: 1.5rem;
    line-height: 1.85rem; }
  li {
    font-size: 1.25rem;
    line-height: 1.5rem; }
  .statement {
    font-size: 2.5rem;
    line-height: 2.75rem;
    font-family: "Lato";
    font-weight: bold;
    text-shadow: 0.2rem 0.2rem 0.2rem rgba(0, 0, 0, 0.1); }
  .small-text {
    font-size: 1.2rem;
    line-height: 1.25rem; }
  h1 {
    font-size: 2.5rem; }
  h2 {
    font-size: 2.25rem; }
  h3 {
    font-size: 2rem; }
  h4 {
    font-size: 1.75rem; } }

@media all and (max-width: 800px) {
  p {
    font-size: 1.25rem;
    line-height: 1.5rem; }
  li {
    font-size: 1.15rem;
    line-height: 1.45rem; }
  .statement {
    font-size: 2rem;
    line-height: 2.5rem;
    font-family: "Lato";
    font-weight: bold;
    text-shadow: 0.2rem 0.2rem 0.2rem rgba(0, 0, 0, 0.1); }
  .small-text {
    font-size: 1.2rem;
    line-height: 1.25rem; }
  h1 {
    font-size: 2.25rem; }
  h2 {
    font-size: 2rem; }
  h3 {
    font-size: 1.75rem; }
  h4 {
    font-size: 1.5rem; } }



