@import url("https://fonts.googleapis.com/css?family=Ubuntu+Mono:400,700");
pre {
  white-space: pre-wrap;
  /* css-3 */
  white-space: -moz-pre-wrap;
  /* Mozilla, since 1999 */
  white-space: -pre-wrap;
  /* Opera 4-6 */
  white-space: -o-pre-wrap;
  /* Opera 7 */
  word-wrap: break-word;
  /* Internet Explorer 5.5+ */ }

.hljs {
  background: rgba(1, 22, 39, 0.85);
  padding: 1.5em 10em 1.5em 4em;
  margin: 1.5em 2em;
  width: auto;
  display: inline-block;
  overflow: auto; }
  @media all and (max-width: 800px) {
    .hljs {
      padding: 1em;
      margin: 1em 2em; } }
  .hljs code {
    padding: 0 !important; }
  .hljs * {
    font-size: 1.5rem;
    font-family: 'Ubuntu Mono', monospace;
    background: none !important; }
    @media all and (max-width: 800px) {
      .hljs * {
        font-size: 1.25rem; } }
  .hljs span {
    color: #F71735; }
  .hljs .keyword {
    font-weight: bold; }
  .hljs .keyword {
    color: #FCAB10; }
  .hljs .function-variable,
  .hljs .function {
    color: #FDFFFE; }
  .hljs .operator,
  .hljs .punctuation {
    color: #A9BCD0; }
  .hljs .comment {
    color: rgba(169, 188, 208, 0.5);
    font-style: italic; }
  .hljs .number {
    color: rgba(252, 171, 16, 0.75); }



